//module
import React from 'react';
import { useSelector,useDispatch } from 'react-redux';
//module
import { operationsContacts } from '../../redux/phonebook';
import { selectorsPhonebook } from '../../redux/phonebook';
//styles
import style from './ContactsList.module.css';

export default function ContactsList() {
  const dispatch = useDispatch()
  
  const contacts = useSelector(selectorsPhonebook.getVisibleContacts)
  const onDeleteContact = (id) => dispatch(operationsContacts.deleteContact(id))

  return (
    <ul className={style.contacts_list}>
    {contacts.map(({ name, id, number }) => (
      <li key={id} className={style.contacts_item}>
        <span className={style.span}>☆  </span> <p className={style.contacts_item_title}>
          {name} : {number}
        </p>
        <button
          className={style.contacts_item_button}
          type="button"
          onClick={()=>onDeleteContact(id)}
        >
          ✖
        </button>
      </li>
    ))}
  </ul>
)
}
