//module
import React,{useEffect} from 'react';
import { useDispatch,useSelector } from 'react-redux';
//component
import Container from '../../components/Container';
import Form from '../../components/Form';
import Stats from '../../components/Stats';
import Filter from '../../components/Filter';
import { operationsContacts } from '../../redux/phonebook';
import { selectorsPhonebook } from '../../redux/phonebook';
import ContactsList from '../../components/ContactsList';

//style
import style from './PhonebooksView.module.css'

export default function PhonebooksView() {
    const dispatch = useDispatch()
    const isLoading = useSelector(selectorsPhonebook.getLoading)
    
    useEffect(() =>dispatch(operationsContacts.featchContacts()), [dispatch])

    return (
            <Container>
                <section className={style.Container}>
                    <h2 className={style.contactsTitle}>Phonebook</h2>
                    <Form />
                    <article className={style.contactsArticle}>
                        <h2 className={style.contactsTitle}>Contacts</h2>
                        <Stats />
                        <Filter />
                        {isLoading && <h5 className={style.contactsTitle} >Loading...</h5>}
                        <ContactsList />
                    </article>
                </section>
            </Container>
        )
}
