//module
import React, { useState } from 'react';
// import PropTypes from 'prop-types';
// import { useSelector } from 'react-redux';
//component
import { useDispatch } from 'react-redux';
import { operationsContacts } from '../../redux/phonebook';
// import { selectorsPhonebook } from '../../redux/phonebook';
//style
import './Form.scss';

export default function Form() {
  const dispatch = useDispatch();
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');

  const handleChange = ({ target }) => {
    const { name, value } = target;

    switch (name) {
      case 'name':
        setName(value);
        break;

      case 'number':
        setNumber(value);
        break;

      default:
        console.warn("dot't hava input with this atribute");
    }
  };

  const handleSubmit = e => {
    e.preventDefault();

    dispatch(operationsContacts.addContact(name, number));

    setName('');
    setNumber('');
  };

  return (
    <form onSubmit={handleSubmit} className="form">
      <label className="label">
        Name and Surname
        <input
          className="input"
          type="text"
          name="name"
          value={name}
          pattern="^[a-zA-Zа-яА-Я]+(([' -][a-zA-Zа-яА-Я ])?[a-zA-Zа-яА-Я]*)*$"
          title="Имя может состоять только из букв, апострофа, тире и пробелов. Например Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan и т. п."
          required
          onChange={handleChange}
          placeholder="☞ Rebekka Lolita"
        />
      </label>
      <label className="label">
        Number
        <input
          className="input"
          type="tel"
          name="number"
          value={number}
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="Номер телефона должен состоять цифр и может содержать пробелы, тире, круглые скобки и может начинаться с +"
          required
          onChange={handleChange}
          placeholder="+45 94 124 2434"
        />
      </label>
      <button className="button">Add contacts</button>
    </form>
  );
}
