//module
import React from 'react';
//component
import { useSelector,useDispatch} from 'react-redux';
import * as actionPhonebook from '../../redux/phonebook/action_phonebook';
import { selectorsPhonebook } from '../../redux/phonebook';
//styles
import style from'./Filter.module.css';

export default function Filter() {
  const dispatch = useDispatch()
  
  const value = useSelector(selectorsPhonebook.getFilter)
  const onChange=({target})=>dispatch(actionPhonebook.filterContact(target.value))

  return (
    <label className={style.label_filter}>
    Filter
    <input
      className={style.input_filter}
      value={value}
      type="text"
      name="filter"
      pattern="^[a-zA-Zа-яА-Я]+(([' -][a-zA-Zа-яА-Я ])?[a-zA-Zа-яА-Я]*)*$"
      onChange={onChange}
    />
  </label>)
}


